import React, { Component } from 'react';
import {BrowserRouter, Route} from 'react-router-dom';


const Library = () =>{
  return (
    <div>hi</div>
  )
}

class App extends Component {
  state = {  }
  render() { 
    return ( 
      <BrowserRouter>
        <Route path="/new" Component={Library} />
      </BrowserRouter>
    );
  }
}
 
export default App;


